// General

.content-width {
    width: 1280px;
    max-width: 90%;
}

.inner-banner-text {
    text-align: center;
}

// * {
//     font-family: 'Montserrat', sans-serif;
// }

.inner-banner-quote {
    font-size: 24px;
    color: white;
    font-weight: 400;
    max-width: 500px;
    position: relative;
    margin: auto;
}

.inner-banner-name {
    font-size: 24px;
    color: white;
    font-weight: 400;
    max-width: 500px;
    text-align: right;
}

.inner-banner-name a {
    color: white;
}

.inner-banner-name::after {
    content: url('http://3.229.143.116/bkdental/wp-content/uploads/2019/11/rateabizlogo.svg');
    position: relative;
    left: 10px;
}

.inner-banner-quote::before {
    content: url('http://3.229.143.116/bkdental/wp-content/uploads/2019/11/left-quote.svg');
    position: relative;
    left: -30px;
    top: 3px;
}


.inner-banner {
    overflow: hidden;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover !important;
    background-position: center !important;
}


@media screen and (max-width: 767px) {
    .inner-banner-quote {
        max-width: 400px
    }
}

@media screen and (max-width: 500px) {
    .inner-banner-quote {
        max-width: 300px;
        font-size: 20px;
    }

    .inner-banner-name {
        text-align: center;
    }
}


//General End

