.margin-reducer {
    margin-top: 0px !important;
    padding-bottom: 0px !important
}

.appointment-hover {
    margin-top: 50px;
    opacity: 1;
    transition: all 0.5s ease;
}

.appointment-hover:hover {
    opacity: 0.5;
}

.position-relative {
    position: relative;
}

.svg-img-sparkletooth figure::after {
    content: " ";
    background-image: url('http://3.229.143.116/bkdental/wp-content/uploads/2019/11/Group-305.svg');
    position: absolute;
    z-index: 1;
    width: 120px;
    height: 120px;
    bottom: -30px;
    right: 80px;
    background-size: cover;
    background-position: center center;
}

.svg-img-sparkletooth figure::before {
    content: " ";
    background-image: url('http://3.229.143.116/bkdental/wp-content/uploads/2019/11/Group-306.svg');
    position: absolute;
    z-index: 2;
    width: 125px;
    height: 120px;
    bottom: -20px;
    right: 70px;
    background-size: cover;
    background-position: center center;
}


.line-height-1 {
    line-height: 0.5em;
}

#boris-snippet {
    max-width: 500px;
}

.home-banner-text {
    position: absolute;
    top: 50%;
    left: calc(20px + 20%);
}


.banner {
    overflow: hidden;
    height: 100vh;
    width: 100%;
    background-size: cover !important;
    background-position: center !important;
}

.services-panel {
    display: grid;
    align-items: center;
    box-shadow: 0px 5px 5px rgba(150,150,150,0.2);
    position: relative;
    background: white;
    grid-template-rows: auto auto;
    grid-area: d;
    display: none;
    width: 100%;
    max-height: 0px;
    height: auto;
    transition: all 0.8s ease;
    overflow: hidden;
    display: grid;
    z-index: 1;
    opacity: 0;
    pointer-events: none;
}

.services-panel:hover {
    display: grid;
}
.general-dentistry {
    width: 100%;
    height: 100%
}


.general-dentistry, .cosmetic-dentistry, .youth-dentistry {
    position: relative;
    margin-bottom: 0;
}

.general-dentistry::after {
    content: "";
    width: 0; 
    height: 0; 
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-bottom: 20px solid white;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: none;
}

.cosmetic-dentistry::after {
    content: "";
    width: 0; 
    height: 0; 
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-bottom: 20px solid white;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: none;

}


.youth-dentistry::after {
    content: "";
    width: 0; 
    height: 0; 
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-bottom: 20px solid white;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: none;

}


.panel-grid {
    max-width: 1000px;
    margin: 0 auto;
    display: grid;
    grid-template-areas: "a b c" 
                         "d d d";
    justify-content: space-around;
    justify-items: center;
    text-align: center;
}


