#general-dentistry:hover ~ .general-dentistry,
#cosmetic-dentistry:hover ~ .cosmetic-dentistry,
#youth-dentistry:hover ~ .youth-dentistry{
    display: none !important;
}

.hover::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: linear-gradient(180deg, #CBCBCB 0%, #5F5F5F 97%, #626262 100%) 0% 0% no-repeat padding-box;
    opacity: 0.85;
    display: block;
}

.hover:hover::before {
    background: transparent linear-gradient(180deg, #0090D0 0%, #225C76 100%) 0% 0% no-repeat padding-box;
}

.home-show-after#general-dentistry,
.home-show-after#cosmetic-dentistry,
.home-show-after#youth-dentistry {
    max-height: 3000px;
    z-index: 2;
    opacity: 1;
    pointer-events: auto;
    padding-top: 100px;
    padding-bottom: 50px;
}

.home-show-after:after {
    display: block;
}

.home-show-after:before {
    display: block;
    background: transparent linear-gradient(180deg, #0090D0 0%, #225C76 100%) 0% 0% no-repeat padding-box;
}

.hover-text {
    display: block;
    max-width: 300px;
    margin: 0 auto;
    width: auto !important;
    position: relative;
}

.hover-text::after {
    content: '';
    position: absolute;
    z-index: 2;
    border: 1px solid white;
    border-left: none;
    border-right: none;
    top: 0px;
    left: 50%;
    bottom: 0px;
    right: 50%;
    transition: all 0.2s linear;
    opacity: 0;
}

.hover:hover .hover-text::after {
    left: -10%;
    right: -10%;
    opacity: 1;
}

.home-show-after.hover .hover-text::after {
    left: -10%;
    right: -10%;
    opacity: 1;
}

@media screen and (max-width: 1280px) {
    .svg-img-sparkletooth figure::after {
        bottom: -30px;
        right: 0px;
    }

    .svg-img-sparkletooth figure::before {
        bottom: -20px;
        right: -10px;
    }

    .general-dentistry {
        height: 200px !important;
        width: 200px !important;
    }
    
    .cosmetic-dentistry {
        height: 200px !important;
        width: 200px !important;
    }
    
    .youth-dentistry {
        height: 200px !important;
        width: 200px !important;
    }
    
    .services-panel .wp-block-columns {
        width: unset;
        margin: 0 auto;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    
    .services-panel .wp-block-column {
        margin: 0px;
        width: 50%;
    }
}

@media screen and (max-width: 768px) {

    .svg-img-sparkletooth figure::after {
        bottom: -10px;
        right: 0px;
        width: 60px;
        height: 60px;
    }

    .svg-img-sparkletooth figure::before {
        bottom: 0px;
        right: -10px;
        width: 65px;
        height: 60px;
    }
    
    .home-banner-text {
        left: 50%;
        transform: translate(-50%, 0%);
    }
    
    
    .home-banner-text h1 {
        font-size: 35px;
    }

    .panel-grid {
        display: grid !important;
        grid-template-areas: "a"
                             "d"
                             "b"
                             "e"
                             "c"
                             "f" !important;
    }
    
    #cosmetic-dentistry {
        grid-area: e;
    }
    
    #youth-dentistry {
        grid-area: f;
    }
    
    .general-dentistry {
        margin-top: 20px;
    }
    
    .cosmetic-dentistry {
        margin-top: 20px;
    }
    
    .youth-dentistry {
        margin-top: 20px;
    }
    
    .services-panel .wp-block-columns {
        width: unset;
        margin: 0 auto;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    
    .services-panel .wp-block-column {
        margin: 0px;
        width: 100%;
    }
    
}

@media screen and (max-width: 767px) {
    .panel-grid {
        display: block;
    }
}

