footer {
    width: 100vw;
    min-height: 166px;
    background: #315F80;
    position: relative;
    left: -20px;
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
}

#menu-footer {
    display: flex;
    list-style: none;

    li{
        margin-right: 2em;

        a {
            text-decoration: none;
            font-weight: 300;
            color: white;
            transition: all 0.3s ease-out

        }

        a:hover {
            color: $secondaryColor;
            transition: all 0.3s ease-out
        }
    }

    li:last-child {
        margin-right: unset;
    }
}


#footer-social {
    list-style: none;
    display: flex;
    color: white;
    position: relative;

    a {
        color: white;
        text-decoration: none;
        margin-right: 2em;
        transition: all 0.3s ease-out

        
    }
    a:hover {
        color: $secondaryColor;
        transition: all 0.3s ease-out
    }
    svg {
        height: 30px;
        width: auto;
    }
    li{
        margin-right: 2em;

        a {
            text-decoration: none;
            font-weight: 300;
            color: white;
        }
    }

}

