#pre-footer {
    overflow: hidden;
    position: relative;
    min-height: 500px;

    img {
        margin: auto;
        display: block;
        min-width: 100%;
        min-height: 500px;
    }

    #pre-footer-content {
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        left: 50%;
        color: white;
        text-align:center;

        #pre-footer-paragraph {
            font-size: 20px;
            font-weight: 400;
        }
    }
}

@media screen and (max-width: 1023px) {
    footer {
        display: flex;
        flex-direction: column-reverse;
    }
}

@media screen and (max-width: 900px) {
    #pre-footer {
        img {
            object-position: -250px;
        }
    }
}


@media screen and (max-width: 700px) {
    #pre-footer {
        img {
            object-position: -450px;
        }
    }
}

@media screen and (max-width: 500px) {
    #pre-footer {
        img {
            object-position: -600px;
        }

        #pre-footer-content {
            #pre-footer-paragraph {
                font-size: 18px;
            }
        }
    }

    #menu-footer {
        flex-direction: column;
        li {
            margin-right: 0;
            margin-top: 20px;
            text-align: center;

        }
    }

    #footer-social {
        margin-top: 30px
    }
}
