* {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    font-family: canada-type-gibson, sans-serif;
}

body:not(header) {
    padding: 0 20px;

}

.mm-open {
    overflow: hidden;
    position: fixed;
}

footer {
    padding: unset;
}

.entry-footer{
    display: none;
}

p {
    font-size: 16px;
    font-weight: 300;
    line-height: 1.7;
}

h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
} 

h1 {
    font-size: 66px;
    color: white;
    font-weight: 500;
}

h2 {
    color: #315F80;
    font-size: 56px;
    font-weight: 500;
}

h3 {
    font-size: 26px;
    color: #315F80;
    font-weight: 500;
    margin-bottom: 20px;
}

h4 {
    font-size: 20px;
    color: #315F80;
    font-weight: 500;
    margin-bottom: 20px;
}

h5 {
    font-size: 16px;
    color: #315F80;
    font-weight: 500;
    margin-bottom: 20px;
}

a {
    color: $primaryColor;
    text-decoration: none;
    transition: all 0.5s ease-out

}

a:hover {
    color: $secondaryColor;
    transition: all 0.3s ease-out
}
#page {
    min-height: 100vh;
    margin-top: -20px;
}

ul.menu {
    padding: 0;
}

.outline-button-link {
    background: white;
    color: $primaryColor;
    padding: 7px 10px;
    border: $primaryColor solid 1px;
    display: inline-block;

    &:not(:last-of-type) {
        margin-right: 10px;
    }

    &:hover {
        background: $primaryColor;
        color: white;
    }
}


.main-cta {
    background: $primaryColor;
    color: white;
    padding: 15px 30px;
    border: $primaryColor 1px solid;
    width: 250px;
    font-weight: 300;
    cursor: pointer;
    transition: all 200ms;
    position: relative;
}

.main-cta::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}



.width100p {
    width: 100%;
}

.main-cta:hover {
    background: white;
    color: $primaryColor;
    cursor: pointer;
    transition: all 200ms;
    border: 1px solid $primaryColor;
}


.secondary-cta {
    background: white;
    color: $primaryColor;
    padding: 15px 30px;
    border: none;
    width: 250px;
    font-weight: 300;
    cursor: pointer;
    transition: all 200ms;
    margin-top: 30px;
}

.secondary-cta:hover {
    background: $primaryColor;
    color: white;
    transition: all 200ms;
}
.fixed-width {
    max-width: 1440px;
}

.grey-background {
    background: $greyBackground;
}

.center-content {
    width: auto;
    margin: auto;
}

.left-right-padding {
    padding-left: $leftRightSpacing;
    padding-right: $leftRightSpacing;
}
.top-bottom-padding {
    padding-top: $generalSpacing;
    padding-bottom: $generalSpacing;
}

.top-bottom-margin {
    margin-top: $generalSpacing;
    margin-bottom: $generalSpacing;
}

.paragraph-margin-below {
    margin-bottom: $paragraphMarginBelow;
}

.condensed-text {
    max-width: $condensedText;
    width: 90%;
}

.condensed-area {
    max-width: 1000px;
}

.display-grid {
    display: grid;;
}

.align-items-center{
    align-items: center !important;
}


.white-text {
    color: white;
}

.position-relative {
    position: relative;
}

.z-index-up-2 {
    z-index: 2;
}

.hover-text {
    color: white;
    z-index: 2;
    position: relative;
}

.margin-top-0 {
    margin-top: 0;
}

.margin-bottom-0 {
    margin-bottom: 0;
}

.stars-margin {
    margin-top: $starsMargin;
    margin-bottom:  $starsMargin;
}


@media screen and (max-width: 767px) {
    p {
        font-size: 16px;
        font-weight: 300;
    }
    
    h1 {
        font-size: 56px;
    }
    
    h2 {
        font-size: 46px;
    }
    
    h3 {
        font-size: 36px;
    }
    
    h4 {
        font-size: 20px;
    }
    
    h5 {
        font-size: 16px;
        color: #315F80;
        font-weight: 500;
        margin-bottom: 20px;
    }
}